import html2canvas from "html2canvas";

export default function posterGenerator(el) {
  // * ÊÉ≥Ë¶Å‰øùÂ≠òÁöÑÂõæÁâáËäÇÁÇπ
  const dom = document.querySelector(el);

  // ÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑcanvas
  const Canvas = document.createElement("canvas");
  const width = dom.offsetWidth; // ÂèØËßÅÂ±èÂπïÁöÑÂÆΩ
  const height = dom.offsetHeight; // ÂèØËßÅÂ±èÂπïÁöÑÈ´ò
  console.log(
    dom,

    dom.offsetWidth,
    dom.offsetHeight
  );
  const scale = window.devicePixelRatio;

  // Â∞ÜCanvasÁîªÂ∏ÉÊîæÂ§ßscaleÂÄçÔºåÁÑ∂ÂêéÊîæÂú®Â∞èÁöÑÂ±èÂπïÈáåÔºåËß£ÂÜ≥Ê®°Á≥äÈóÆÈ¢ò
  Canvas.width = width * scale;
  Canvas.height = height * scale;
  Canvas.getContext("2d").scale(scale, scale);

  html2canvas(dom, {
    canvas: Canvas,
    scale: scale * 0.5,
    useCORS: true,
    width: width + "px",
    hegiht: height + "px",
  }).then((canvas) => {
    const context = canvas.getContext("2d");
    // ÂÖ≥Èó≠ÊäóÈîØÈΩøÂΩ¢
    context.mozImageSmoothingEnabled = false;
    context.webkitImageSmoothingEnabled = false;
    context.msImageSmoothingEnabled = false;
    context.imageSmoothingEnabled = false;
    // test the canvas

    // canvasËΩ¨Âåñ‰∏∫ÂõæÁâá;
    const img = canvas2Image(canvas, canvas.width, canvas.height);
    document.body.appendChild(img);
    img.classList.add("newposter");
    img.style.cssText =
      "width:38.2rem;height: 54rem;position:fixed;top:50%;left:50%;transform: translate(-50%, -50%);opacity:1;object-fit:cover;z-index:20;";
  });
}

function canvas2Image(canvas, width, height) {
  const retCanvas = document.createElement("canvas");
  const retCtx = retCanvas.getContext("2d");
  retCanvas.width = width;
  retCanvas.height = height;
  retCtx.drawImage(canvas, 0, 0, width, height, 0, 0, width, height);

  const img = document.createElement("img");
  img.src = retCanvas.toDataURL("image/jpeg"); // ÂèØ‰ª•Ê†πÊçÆÈúÄË¶ÅÊõ¥ÊîπÊ†ºÂºè
  return img;
}
